/* ========================= GLOBAL ========================= */

*{
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  font-size: 12px;
  font-weight: 400;
}
body{
  margin: 0;
}
h1{
  font-size: 36px;
}
h3{
  font-size: 24px;
  
}
h5{
  font-size: 12px;
  font-weight: 700;
}
a{
  text-decoration: none;
  color: black;
}
.container{
  margin: 0 auto 25px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 1180px;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  padding-top: 20px;
}
.underline{
  width: 100vw;
  height: 1px;
  content: '';
  background: #e1e1e1;
}

.btn{
  border: 1px solid white;
  padding: 16px 37px;
  cursor: pointer;
}
.btn:hover{
  border-color:  #ddd3a9;   
}
.margin-top{
  margin-top: 10px;
}

.pointer{
  cursor: pointer;
}

.modal{
  position: fixed;
  z-index: 20;
  top:0;
  left:0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  
}
.submodal{
  width: 300px;
  height: 200px;
  background-color: #e1e1e1;
  margin-top: 300px;
  padding: 50px 10px 10px;
  align-content: center;
  text-align: center;
  border: 2px solid #868f93 ;
  border-radius: 10px;
}
.submodal>p{
  margin-bottom: 40px;
}
.submodal>.btn{
  margin: 5px;
}
.width-100{
  width: 100%;
}
/*============================================GLOBAL END==========================================*/
/* =========================================  HEADER START ============================ */

.header_overline{
  margin: 0 auto 25px;
  background-color: #d9e1e6;
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  color: #868f93;
  font-size: 10px;
  padding: 10px 0;
}
header{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;
  width: 100%;
}
.nav{
  display: block;
  width: 14px;
  height: 14px;
  cursor: pointer;
}
.nav_menu{
  position: absolute;
  height: 2px;
  width: 12px;
  background-color: black;
  content: '';
}
.nav_menu::before{
  position: absolute;
  top: 4px;
  content: '';
  height: 2px;
  width: 12px;
  background-color: black;
}
.nav_menu::after{
  position: absolute;
  top: -4px;
  content: '';
  height: 2px;
  width: 12px;
  background-color: black;
}
.log-links{
  position: absolute;
  top:0;
  right:150px;
  border: 1px solid #868f93;
}
.log-link{
  margin: 0 5px;
  
}

.list>ul{
  display: flex;
  flex-direction: row;
}
.list>ul>li{
  list-style-type: none;
  margin: 0 30px;
}
.like_search{
  display: flex;
  flex-direction: row;
}

.allign{
  
  padding-right: 5px;
  margin-left: 5px;  
}
.border_right{
  border-right: 1px solid black;
}
.icon{
  width: 24px;
  height: 24px;
  cursor: pointer  ;
}
.searching_window{
  display: block;
  width: 100%;
  padding: 10px 20px;
  outline: none;
  font-size: 24px;
}
.show{
  display: block;
  position: absolute;
  right: 0;
}
.hidden{
  display: none !important; 
}
.card{
  width: 250px;
  padding:10px 10px 30px;
  background-color: #e1e1e1;
  position: fixed;
  right: 0;
  text-align: center;
}
.card-item{
  padding-bottom: 30px;
  border-bottom: 1px solid #868f93
}
.form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  width: 400px;
  flex-wrap: wrap;
}
.form>input{
  outline: none;
}
.form>.btn {
  margin: 10px;
  
}

.form>div>label{
  display: block;
  width: 50px;
}
.form>div{
  display: flex;
  margin-left: 15px;
  justify-content: center;
  
}
.form>div>input{
  margin: 0;
}

/* =========================== HEADER END ======================== */

/*================================MAIN START=========================*/
.main{
  padding-top: 40px
}
.products-item-list{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0
}
.product-item-short{
  margin: 20px;
  width: 20%;
  border: 1px solid  #e1e1e1;
  text-align: center;
  padding-bottom: 30px;
}

.small-img{
  width: 100%;
  height: 200px;
  
}


/*================================MAIN END=========================*/
/*================================ITEM PAGE START ==================*/
.img_examples{
  width: 50%;
}

.general_img{
  width: 630px;
  height: 680px;

}
.additional_img{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
  width: 630px;
}
.img{
  width: 140px;
  height: 165px;
  margin: 0 10px;
}
.img:first-child{
  margin-left: 0;
}
.img:last-child{
  margin-right: 0;
}
.general_img, .img{
  background-color: #e1e1e1;
}
.product_info{
  width: 45%;
  padding-top: 100px;
  color: #555555;
  margin-top: -190px;
  font-weight: 300;
  justify-content: space-between;
}
.product_info>h3{
  font-weight: 700;
  line-height: 30px;
  color: black;

}
.review{
  margin-left: 5px;
  font-weight: 400;
  cursor: pointer;
}
.review:hover{
  text-decoration: underline;
}
.product_info>.price{
  color: #68899a;
}
.price, .product_type{
  color: #68899a;
  cursor: pointer;
}
.price:hover, .product_type:hover{
  text-decoration: underline;
}
.product_info>p, .product_info>ul>li{
  font-weight: 300;
  
}
.product_selection{
  display: flex;
  flex-direction: row;
  margin-top: 40px;
  justify-content: space-between;
  
}
.selection{
  width: 100px;
  height: 50px;
  margin: 0 10px;
  
}
.quantity{
  display: flex;
  border: 1px solid gray;
  justify-content: space-between;
}
.number{
  text-align: center;
  padding-top: 3px;
  width: 40px;
}
.form-btn{
  width: 30px;
  height: 20px;
  background-color: white;
  border: none;
  outline: none;
  align-items: center; 
  cursor: pointer;
}

.clear{
  cursor: pointer;
  margin-top: 40px;
}
.clear:hover{
  text-decoration: underline;
}
.cart_and_wish{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}
.cart_and_wish>div{
  width: 214px;
  height: 55;
  text-transform: uppercase;
  color: black;
  text-align: center;
  border: 1px solid gray;
  padding: 22px 0;
  cursor: pointer;
}
.cart_and_wish>div:hover{
  background-color: black;
  color: white;
  border-color: black;
}
.share{
  width: 100%;
  border: 1px solid gray;
  padding: 20px 0;
  text-align: center;
  cursor: pointer;
}
.red{
  color: red;
}
/*================================ITEM PAGE END ==================*/